import util from '../../common/util.js'
export default {
	data() {
		return {
			selected:0,
			cutMoney:0,
			userinfo:'',
			couponList:[]
		}
	},
	onLoad() {
		uni.showLoading({
			title:'加载中...',
			mask:true	
		})
		this.userinfo = uni.getStorageSync('userinfo');
		var user_id = this.userinfo.user_id;
		var url = this.serverURL;
		var _this = this;
		uni.request({
			url: url + 'home/user/get_user_coupon',
			method:'POST',
			data:{
				user_id: user_id
			},
			success(res) {
				console.log(res)
				if(res.data.code==100){
					_this.couponList = res.data.msg;
					uni.hideLoading();
				}
			},
			fail(res) {
				console.log(res);
			}
		})
	},
	methods: {
		selectCoupon(id,money) {
			var _this = this;
			_this.selected = id;
			//_this.cutMoney = money;
			_this.cutMoney = {
				id:id,
				money:money
			};
			uni.setStorage({
				key:"cutMoney",
				data:_this.cutMoney,
				success() {
					console.log('存储成功')
				}
			})
			if(id==0){
				uni.navigateBack();
			}
			
		},
		isOverdue(time) {
			return util.isOverdue(time);
		},
		setTime(time) {
			return util.formatTime(time);
		}
	}
}
